/* @include modal_style; */
@mixin modal_style
{
	#woob2b_modal{
		position:fixed;
		width:100%;
		height:100%;
		top:0;
		left:0;
		z-index:1000;
		background:rgba(255,255,255,0.7);
		.close-button{
			position:absolute;
			top:5px;
			right:5px;
			display:block;
			width:35px;
			height:35px;
			cursor:pointer;
			svg{
				fill:black;
			}
		}
		.inner{
			position:relative;
			width:80%;
			max-width:400px;
			min-height:200px;
			margin: 30vh auto;
			background:rgba(255,255,255,1);
			@include box_shadow(1px,2px,3px,#999);
			text-align:center;
			padding:40px;
			.h3{
				font-size:27px;
				line-height:34px;
			}
			p{
				display:block;
				width:100%;
				margin:15px 0 30px 0;
				font-size:17px;
				line-height:22px;
				text-align:center;
			}
			.buttonh{
				position:relative;
				display:block;
				clear:both;
				width:100%;
				min-height:25px;
				margin-top:25px;
				margin-bottom:25px;
				&.multi{
					.btn{
						&:nth-child(1){
							margin-right:6px;
						}
					}

				}

			}
		}

	}
}